.product {
  padding-top: 18px;
  padding-bottom: 42px;
  position: relative;
  z-index: 1;
  color: white;
  @include media-breakpoint-up(md) {
    padding-top: 50px;
    padding-bottom: 60px;
  }

  &::after {
    @include pseudo;
    position: absolute;
    top: 0;
    right: -15px;
    bottom: 0;
    left: -15px;
    z-index: -1;
    background: $color-navy;
    @include media-breakpoint-up(xss) {
      width: 100vw;
      right: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__logo {
    margin: 0 -5px 42px;
    position: relative;
    @include media-breakpoint-up(sm) {
      margin-right: 0;
      margin-left: 0;
    }
    @include media-breakpoint-up(xl) {
      margin-bottom: 47px;
      left: -6px;
    }

    figcaption {
      margin-bottom: 12px;
      display: block;
      font-size: 26px;
      line-height: 40px;
      font-weight: bold;
      text-align: center;
      @include media-breakpoint-up(xl) {
        margin-bottom: 20px;
        font-size: 30px;
        line-height: 38px;
      }
    }

    img {
      width: 345px;
      max-width: 100%;
      height: auto;
      margin: 0 auto;
      display: block;
      @include user-select;
      @include media-breakpoint-up(xl) {
        width: 470px;
      }
    }
  }

  &__image {
    margin: 0 0 32px;
    position: relative;
    z-index: 1;
    @include media-breakpoint-up(lg) {
      margin: 0;
      left: -15px;
    }
    @include media-breakpoint-up(xl) {
      left: 0;
    }

    img {
      width: 100%;
      max-width: 330px;
      height: auto;
      margin: 0 auto;
      position: relative;
      display: block;
      @include user-select;
      @include media-breakpoint-up(sm) {
        max-width: 420px;
      }
      @include media-breakpoint-up(lg) {
        max-width: 450px;
      }
      @include media-breakpoint-up(xl) {
        width: 600px;
        max-width: none;
        margin: 0 -16px 0 -29px;
        top: 9px;
      }
      @include media-breakpoint-up(xxl) {
        width: 700px;
        margin: 0 -6px 0 -139px;
      }

      &.mobile {
        @include media-breakpoint-up(xl) {
          display: none;
          visibility: hidden;
        }
      }
      &.desktop {
        display: none;
        visibility: hidden;
        @include media-breakpoint-up(xl) {
          display: block;
          visibility: visible;
        }
      }
    }
  }

  &__content {
    font-size: 18px;
    line-height: 25px;
    position: relative;
    z-index: 3;
    @include media-breakpoint-up(lg) {
      max-width: 480px;
    }
    @include media-breakpoint-up(xl) {
      left: 8px;
      font-size: 20px;
      line-height: normal;
    }

    strong {
      color: #ffc221;
    }

    p {
      margin-bottom: 29px;
      @include media-breakpoint-up(xl) {
        margin-bottom: 26px;
      }
    }

    ul {
      padding: 0;
      margin: 0;
      list-style: none;
      line-height: 26px;
      font-weight: 600;
      position: relative;
      @include media-breakpoint-up(xl) {
        font-size: 18px;
        top: -2px;
      }

      li {
        padding-left: 23px;
        position: relative;

        &::before {
          @include pseudo;
          width: 14px;
          height: 11px;
          position: absolute;
          top: 8px;
          left: 0;
          background: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxNCIgaGVpZ2h0PSIxMSIgZmlsbD0ibm9uZSI+PHBhdGggc3Ryb2tlPSIjRkZDMjIxIiBzdHJva2Utd2lkdGg9IjIiIGQ9Im0xIDUgMy44MSA0IDcuNjE5LTgiLz48L3N2Zz4=') no-repeat center center;
          background-size: contain;
        }
      }
    }
  }
}